import * as EventHandle from "./generaleventhandle.js";
export default function (InputValue, InputUnit, OutputUnit = "m") {
	let SIValue;
	let OutputValue;
	let Invalid = false;
	if (typeof InputValue != "number") {
		EventHandle.InvalidInputValue("Length", InputValue, "number");
		Invalid = true;
		InputValue = 1;
	};
	switch (InputUnit) {
		case "m": case "Metre": case "Meter": // SIUnit
			SIValue = InputValue;
			break;
		case "Ym": case "Yottametre": case "Yottameter":
			SIValue = InputValue * Math.pow(10, 24);
			break;
		case "Zm": case "Zettametre": case "Zettameter":
			SIValue = InputValue * Math.pow(10, 21);
			break;
		case "Em": case "Exametre": case "Exameter":
			SIValue = InputValue * Math.pow(10, 18);
			break;
		case "Pm": case "Petametre": case "Petameter":
			SIValue = InputValue * Math.pow(10, 15);
			break;
		case "Tm": case "Terametre": case "Terameter":
			SIValue = InputValue * Math.pow(10, 12);
			break;
		case "Gm": case "Gigametre": case "Gigameter":
			SIValue = InputValue * Math.pow(10, 9);
			break;
		case "Mm": case "Megametre": case "Megameter":
			SIValue = InputValue * Math.pow(10, 6);
			break;
		case "km": case "㎞": case "㏎": case "Kilometre": case "Kilometer":
			SIValue = InputValue * Math.pow(10, 3);
			break;
		case "hm": case "Hectometre": case "Hectometer":
			SIValue = InputValue * Math.pow(10, 2);
			break;
		case "dam": case "Decametre": case "Decameter":
			SIValue = InputValue * Math.pow(10, 1);
			break;
		case "dm": case "Decimetre": case "Decimeter":
			SIValue = InputValue * Math.pow(10, -1);
			break;
		case "cm": case "㎝": case "Centimetre": case "Centimeter":
			SIValue = InputValue * Math.pow(10, -2);
			break;
		case "mm": case "㎜": case "Millimetre": case "Millimeter":
			SIValue = InputValue * Math.pow(10, -3);
			break;
		case "um": case "µm": case "Micrometre": case "Micrometer":
			SIValue = InputValue * Math.pow(10, -6);
			break;
		case "nm": case "Nanometre": case "Nanometer":
			SIValue = InputValue * Math.pow(10, -9);
			break;
		case "pm": case "Picometre": case "Picometer":
			SIValue = InputValue * Math.pow(10, -12);
			break;
		case "fm": case "Femtometre": case "Femtometer":
			SIValue = InputValue * Math.pow(10, -15);
			break;
		case "am": case "Attometre": case "Attometer":
			SIValue = InputValue * Math.pow(10, -18);
			break;
		case "zm": case "Zeptometre": case "Zeptometer":
			SIValue = InputValue * Math.pow(10, -21);
			break;
		case "ym": case "Yoctometre": case "Yoctometer":
			SIValue = InputValue * Math.pow(10, -24);
			break;
		case "in": case "Inch":
			SIValue = InputValue * 0.0254;
			break;
		case "yd": case "Yard":
			SIValue = InputValue * 0.9144;
			break;
		case "A": case "Å": case "Ångström": case "Angstrom":
			SIValue = InputValue * Math.pow(10, -10);
			break;
		case "ft": case "Foot":
			SIValue = InputValue * 0.3048;
			break;
		case "mi": case "Mile":
			SIValue = InputValue * 1609.344;
			break;
		case "nmi": case "NM": case "M": case "Nautical":
			SIValue = InputValue * 1852;
			break;
		case "ly": case "LightYear":
			SIValue = InputValue * (9.4607 * Math.pow(10, 15));
			break;
		case "Stud":
			SIValue = InputValue * 0.28;
			break;
		default:
			EventHandle.InvalidInputUnit("Length", InputUnit);
			Invalid = true;
			SIValue = 1;
			break;
	};
	switch (OutputUnit) {
		case "m": case "Metre": case "Meter": // SIUnit
			OutputValue = SIValue;
			break;
		case "Ym": case "Yottametre": case "Yottameter":
			OutputValue = SIValue / Math.pow(10, 24);
			break;
		case "Zm": case "Zettametre": case "Zettameter":
			OutputValue = SIValue / Math.pow(10, 21);
			break;
		case "Em": case "Exametre": case "Exameter":
			OutputValue = SIValue / Math.pow(10, 18);
			break;
		case "Pm": case "Petametre": case "Petameter":
			OutputValue = SIValue / Math.pow(10, 15);
			break;
		case "Tm": case "Terametre": case "Terameter":
			OutputValue = SIValue / Math.pow(10, 12);
			break;
		case "Gm": case "Gigametre": case "Gigameter":
			OutputValue = SIValue / Math.pow(10, 9);
			break;
		case "Mm": case "Megametre": case "Megameter":
			OutputValue = SIValue / Math.pow(10, 6);
			break;
		case "km": case "㎞": case "㏎": case "Kilometre": case "Kilometer":
			OutputValue = SIValue / Math.pow(10, 3);
			break;
		case "hm": case "Hectometre": case "Hectometer":
			OutputValue = SIValue / Math.pow(10, 2);
			break;
		case "dam": case "Decametre": case "Decameter":
			OutputValue = SIValue / Math.pow(10, 1);
			break;
		case "dm": case "Decimetre": case "Decimeter":
			OutputValue = SIValue / Math.pow(10, -1);
			break;
		case "cm": case "㎝": case "Centimetre": case "Centimeter":
			OutputValue = SIValue / Math.pow(10, -2);
			break;
		case "mm": case "㎜": case "Millimetre": case "Millimeter":
			OutputValue = SIValue / Math.pow(10, -3);
			break;
		case "um": case "µm": case "Micrometre": case "Micrometer":
			OutputValue = SIValue / Math.pow(10, -6);
			break;
		case "nm": case "Nanometre": case "Nanometer":
			OutputValue = SIValue / Math.pow(10, -9);
			break;
		case "pm": case "Picometre": case "Picometer":
			OutputValue = SIValue / Math.pow(10, -12);
			break;
		case "fm": case "Femtometre": case "Femtometer":
			OutputValue = SIValue / Math.pow(10, -15);
			break;
		case "am": case "Attometre": case "Attometer":
			OutputValue = SIValue / Math.pow(10, -18);
			break;
		case "zm": case "Zeptometre": case "Zeptometer":
			OutputValue = SIValue / Math.pow(10, -21);
			break;
		case "ym": case "Yoctometre": case "Yoctometer":
			OutputValue = SIValue / Math.pow(10, -24);
			break;
		case "in": case "Inch":
			OutputValue = SIValue / 0.0254;
			break;
		case "yd": case "Yard":
			OutputValue = SIValue / 0.9144;
			break;
		case "A": case "Å": case "Ångström": case "Angstrom":
			OutputValue = SIValue / Math.pow(10, -10);
			break;
		case "ft": case "Foot":
			OutputValue = SIValue / 0.3048;
			break;
		case "mi": case "Mile":
			OutputValue = SIValue / 1609.344;
			break;
		case "nmi": case "NM": case "M": case "Nautical":
			OutputValue = SIValue / 1852;
			break;
		case "ly": case "LightYear":
			OutputValue = SIValue / (9.4607 * Math.pow(10, 15));
			break;
		case "Stud":
			OutputValue = SIValue / 0.28;
			break;
		default:
			EventHandle.InvalidOutputUnit("Length", OutputUnit);
			Invalid = true;
			break;
	};
	if (Invalid == true) {
		OutputValue = undefined;
	};
	return OutputValue;
};
