import * as EventHandle from "./generaleventhandle.js";
import SmartStringify from "./smartstringify.js";
export default function (InputValue, InputRadix, OutputRadix = 10) {
	let SIValue;
	let OutputValue;
	let Invalid = false;
	if (!((Number.isInteger(InputValue) == true && InputValue >= 0) || (typeof InputValue == "string" && InputValue.charAt(0) !== "-"))) {
		EventHandle.InvalidInputValue("Radix", InputValue, "number, integer, and positive number; or string");
		Invalid = true;
		InputValue = 0;
	};
	if (Number.isInteger(InputRadix) == true && InputRadix >= 2 && InputRadix <= 36 && Number.isInteger(OutputRadix) == true && OutputRadix >= 2 && OutputRadix <= 36) {
		SIValue = parseInt(InputValue, InputRadix);
		if (Number.isNaN(SIValue) == false) {
			OutputValue = SIValue.toString(OutputRadix);
			if (OutputRadix >= 2 && OutputRadix <= 10) {
				OutputValue = Number(OutputValue);
			};
		} else {
			OutputValue = undefined;
		};
	} else {
		if (Number.isInteger(InputRadix) != true || InputRadix < 2 || InputRadix > 36) {
			console.error("[JavaScript Macro - Converter - Radix] Invalid input unit \"" + SmartStringify(InputRadix) + "\"! Input radix must be type of integer number, and between 2 and 36!");
		};
		if (Number.isInteger(OutputRadix) != true || OutputRadix < 2 || OutputRadix > 36) {
			console.error("[JavaScript Macro - Converter - Radix] Invalid output unit \"" + SmartStringify(OutputRadix) + "\"! Output radix must be type of integer number, and between 2 and 36!");
		};
		Invalid = true;
	};
	if (Invalid == true || Number.isNaN(OutputValue) == true) {
		OutputValue = undefined;
	};
	return OutputValue;
};
