import SmartStringify from "./smartstringify.js";
export function InvalidInputValue(ConverterName, InputValue, InputValueType) {
	console.error("[JavaScript Macro - Converter - " + ConverterName + "] Invalid input value \"" + SmartStringify(InputValue) + "\"! Input value must be type of " + InputValueType + "!");
};
export function InvalidInputUnit(ConverterName, InputUnit) {
	console.error("[JavaScript Macro - Converter - " + ConverterName + "] Invalid input unit \"" + SmartStringify(InputUnit) + "\"! Input unit must be a standard unit!");
};
export function InvalidOutputUnit(ConverterName, OutputUnit) {
	console.error("[JavaScript Macro - Converter - " + ConverterName + "] Invalid output unit \"" + SmartStringify(OutputUnit) + "\"! Output unit must be a standard unit!");
};
